* {
  margin: 0px;
  padding: 0px;
  font-family: sans-serif;
  font-size: 14px;
  font-weight: 100;
  box-sizing: border-box;
}

.app-container {
  display: flex;
}

.header {
  display: flex;
  height: 60px;
  /* background-color: rgb(211, 211, 211); */
  align-items: center;
}

.header-left {
  width: 380px;
  padding-left: 12px;
}

.heading {
  font-size: 24px;
}

.header-right {
  margin-left: 32px;
  width: 100%;
  display: flex;
  justify-content: space-between;
}

.sort-menu {
  margin-top: 16px;
  margin-right: 24px;
}

/****** LEFT SIDE *******/

.left-container {
  padding: 12px;
  width: 380px;
  /* background-color: rgb(255, 149, 149); */
}

.accordians-container {
  margin-top: 26px;
}

/****** RIGHT SIDE *******/

.right-container {
  width: 100%;
  padding: 12px;
}

.cards-container {
  display: flex;
  flex-wrap: wrap;
}

.card {
  margin: 24px;
  margin-bottom: 64px;
  font-size: 14px;
  width: 220px;
  height: 300px;
  /* border: 1px solid black; */
}

.colour-tile {
  height: 200px;
}

.card-information {
  /* padding: 12px; */
  padding-top: 12px;
  text-transform: uppercase;
}

.card-information-item {
  /* padding: 12px; */
  padding-top: 3px;
  text-transform: uppercase;
  color: #888;
}

.type {
  margin-top: 12px;
  margin-bottom: 12px;
  padding-top: 6px;
  padding-bottom: 3px;
  color: rgb(29, 29, 29);
  font-size: 12px;
  font-weight: 400;
  width: 50%;
}

.date {
  color: #999;
  font-size: 12px;
}

.time {
  color: #999;
  font-size: 12px;
}
